export class Racket {
    private m_ctx:any;
    private m_x:number;
    private m_y:number;
    private m_w:number;
    private m_h:number;    
    private m_speed:number
    private m_left:boolean;
    private m_right:boolean;

    constructor(ctx, x, y, w, h) {
        this.m_ctx = ctx;
        this.m_x = x;
        this.m_y = y;
        this.m_w = w;
        this.m_h = h;
        this.m_speed = 6;
        this.m_left = false;
        this.m_right = false;
    }

    public keyBoardEvent() {
        window.addEventListener("keydown", (e) => {
            switch(e.keyCode) {
                case 37:
                    this.m_left = true;
                break;

                case 39:
                    this.m_right = true;
                break;
            }
        }, false);

        window.addEventListener("keyup", (e) => {
            switch(e.keyCode) {
                case 37:
                    this.m_left = false;
                break;

                case 39:
                    this.m_right = false;
                break;
            }
        }, false);
    }

    public drawRacket() {
        this.m_ctx.fillStyle = "#000000";
        this.m_ctx.fillRect(this.m_x, this.m_y, this.m_w, this.m_h);
    }

    public moveRacket() {
        this.m_ctx.fillStyle = "#000000";
        if(this.m_left) {
            this.m_ctx.fillRect(this.m_x-=this.m_speed, this.m_y, this.m_w, this.m_h);
        }

        if(this.m_right) {
            this.m_ctx.fillRect(this.m_x+=this.m_speed, this.m_y, this.m_w, this.m_h);
        }        
    }
}


